/* Profiles Popup Styling - Profiles.css */
.profiles-popup {
  position: absolute;
  top: 70px;
  left: 160px;
  background: white;
  border-radius: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  padding: 15px;
  min-width: 225px;
  min-height: 300px;
  z-index: 200;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 3px solid #7a8aff;
}
